import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route } from "react-router-dom";

import "./style.css";
//import "https://cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.4.1/semantic.min.css";
import Profile from "./views/profile";
import Home from "./views/home";
import BlogPost from "./views/blog-post";
import ContactFormular from "./views/contact_formular";
import Homepage from "./views/homepage";

const App = () => {
  return (
    <Router>
      <div>
        <Route exact component={Profile} path="/profile" />
        <Route exact component={Home} path="/home" />
        <Route exact component={BlogPost} path="/blog-post" />
        <Route exact component={ContactFormular} path="/contact_formular" />
        <Route exact component={Homepage} path="/" />
      </div>
    </Router>
  );
};

ReactDOM.render(<App />, document.getElementById("app"));
